
.principal-container {
    height: 100%;
    width: 100%;
}

.list-container {
    height: 100%;
    width: 30%;
    min-width: 6.5rem;
    max-width: 15rem;
    padding-top: 1.5%;
    position: relative;
    overflow: visible;
}

.list {
    
   width: 100%;
   height: 97.5%;
   position: relative;
   left: -5px;
    overflow-y: scroll;
    overflow-x: hidden;
    z-index: 4;
    scrollbar-color:red black;
    scrollbar-width: thin;
    padding: 1.5%;
    direction:rtl;
}

.list-item {
    width: 100%;
    max-width: 15rem;
    height: 18vh;
    min-height: 4rem;
    max-height: 10rem;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: whitesmoke;
    font-weight: bold;
    color: black;
    font-size: 2vmax;
    user-select: none;
    border: 1px solid #000000;
    transition: 500ms;
}

.selected-list-item {
    width: 100%;
    max-width: 15rem;
    height: 18vh;
    min-height: 4rem;
    max-height: 10rem;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    text-align: center;
    background: whitesmoke;
    font-weight: bold;
    color: black;
    font-size: 2vmax;
    user-select: none;
    border: none;
    transition: 350ms;
    transform: translate(-110%);
}

.selected-item {
    width: 50%;
    max-width: 15rem;
    min-width: 3rem;
    height: 18vh;
    min-height: 3rem;
    max-height: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    text-align: center;
    background: whitesmoke;
    font-weight: bold;
    color: black;
    font-size: 2vmax;
    user-select: none;
    position: absolute;
    border: 1px solid #000000;
    left: 53%;
    transition: 350ms;

}

.list::-webkit-scrollbar {
    width: 0.5rem;
    
  }
   
  .list::-webkit-scrollbar-thumb {
    background-color: rgba(255, 0, 0, 0.616);
  }